import React from "react";
import { useForm } from "react-hook-form";
import { useRecoilValue, useSetRecoilState } from "recoil";
import styled from "styled-components";
import { categoryState, categoryStateNotAll, toDoState } from "../../atoms";

const FlexDiv = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
`;

interface IUseForm {
    errors: {
        password?: {
            message: string;
        };
        value: {
            message: string;
        };
        passwordCheck?: {
            message: string;
        };
        email?: {
            message: string;
        };
    };
    value: string;
    password?: string;
    passwordCheck?: string;
    email?: string;
}

const CreateToDo = () => {
    const setToDos = useSetRecoilState(toDoState);
    const category = useRecoilValue(categoryState);

    const {
        register,
        handleSubmit,
        formState: { errors },
        setError,
        reset,
    } = useForm<IUseForm>();

    const onValid = (data: IUseForm) => {
        if (data.password !== data.passwordCheck) {
            setError(
                "passwordCheck",
                { message: " ." },
                {
                    shouldFocus: true,
                }
            );
        }
        setToDos((prev) => [
            {
                userid: data.value,
                // email: data.email,
                // password: data.passwordCheck,
                id: Date.now(),
                category,
            },
            ...prev,
        ]);
        // setValue("password","");
        // reset({password:""})
        reset();
    };

    return (
        <div>
            <form onSubmit={handleSubmit(onValid)}>
                <div>
                    <input
                        {...register("value", {
                            required: true,
                            minLength: {
                                value: 5,
                                message: "5 .",
                            },
                        })}
                        placeholder=""
                    />
                    <div>
                        <span>{errors?.value?.message}</span>
                    </div>
                </div>
                {/* <div>
                    <input
                        {...register("password", {
                            required: true,
                            pattern: {
                                value: /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/,
                                message:
                                    "8   1 ",
                            },
                        })}
                        placeholder=""
                    />
                    <div>
                        <span>{errors?.password?.message}</span>
                    </div>
                </div>
                <div>
                    <input
                        {...register("passwordCheck", {
                            required: true,
                        })}
                        placeholder=" "
                    />
                    <div>
                        <span>{errors?.passwordCheck?.message}</span>
                    </div>
                </div>
                <div>
                    <input
                        {...register("email", {
                            required: "  ",
                            validate: {
                                noEmailS: (value) =>
                                    !value.includes("@")
                                        ? " "
                                        : true,
                                noEmailS1: (value) =>
                                    !value.includes(".")
                                        ? " "
                                        : true,
                            },
                        })}
                        placeholder=""
                    />
                    <div>
                        <span>{errors?.email?.message}</span>
                    </div>
                </div> */}
                <FlexDiv>
                    <button></button>
                </FlexDiv>
            </form>
        </div>
    );
};

export default CreateToDo;
